import Vue from 'vue'
import App from './App'
import router from './router'
import ImgLazyLoad from 'vue-lazyload'
import VueAwesomeSwiper from 'vue-awesome-swiper'
Vue.use(VueAwesomeSwiper)
Vue.config.productionTip = false
import css1 from '@/assets/css/base.css'
import css2 from '@/assets/css/animate.css'
import css4 from '@/assets/css/style.css'
import css5 from '@/assets/css/responsive.css'
import js1 from '@/assets/js/jquery'
import js4 from '@/assets/js/wow'
import js3 from '@/assets/js/page'

Vue.use(ImgLazyLoad)
    /* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    components: { App },
    template: '<App/>'
})


