import Vue from 'vue'
import Router from 'vue-router'
import index from '@/views/index'
import link from '@/views/link'
import about from '@/views/about'
import cases from '@/views/case'
import riskManagement from '@/views/riskManagement'
import solution from '@/views/solution'

Vue.use(Router)

export default new Router({
    routes: [{
        path: '/index',
        name: 'index',
        component: index
    }, {
        path: '/link',
        name: 'link',
        component: link
    }, {
        path: '/about',
        name: 'about',
        component: about
    }, {
        path: '/cases',
        name: 'cases',
        component: cases
    }, {
        path: '/riskManagement',
        name: 'riskManagement',
        component: riskManagement
    }, {
        path: '/solution',
        name: 'solution',
        component: solution
    }, ]
})


